<template>
  <div class="toast">{{ message }}</div>
</template>
<script>
import { reactive, toRefs } from 'vue'
export default {
  props: ['message']
}
// 对于Toaser组件相关的配置
export const useToasetEffect = () => {
  const toasetData = reactive({
    show: false,
    toasetMessage: ''
  })
  // 是否展示失败框函数
  // 通过改变toasetData数据
  const showToaset = (message) => {
    toasetData.show = true
    toasetData.toasetMessage = message
    setTimeout(() => {
      toasetData.show = false
      toasetData.toasetMessage = ''
    }, 2000)
  }
  const { show, toasetMessage } = toRefs(toasetData)
  return { show, toasetMessage, showToaset }
}
</script>
<style lang='scss'>
@import '../style/viriables.scss';
.toast {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 0.1rem;
  background-color: rgba(0, 0, 0, 0.35);
  border-radius: 0.05rem;
  color: $bg-color;
}
</style>
