<template>
  <div class="order">
    <div class="order__price">
      实付金额<strong>￥{{ calculations.price }}</strong>
    </div>
    <div class="order__btn" @click="() => handleSubmitClick(true)">
      提交订单
    </div>
  </div>
  <div class="mask" v-if="showConfirm" @click="() => handleSubmitClick(false)">
    <div class="mask__content" @click.stop>
      <h3 class="mask__content__title">确认要离开收银台?</h3>
      <p class="mask__content__desc">请尽快完成支付，否则将取消订单</p>
      <div class="mask__content__btns">
        <div class="mask__content__btn" @click="() => handleConfirmOrder(true)">
          取消订单
        </div>
        <div
          class="mask__content__btn"
          @click="() => handleConfirmOrder(false)"
        >
          立即支付
        </div>
      </div>
    </div>
    <Toast v-if="show" :message="toasetMessage" />
  </div>
</template>
<script>
import { useCommonCartEffect } from '../../effects/cartEffects'
import Toast, { useToasetEffect } from '../../components/Toast'
import { useRoute, useRouter } from 'vue-router'
import { post } from '../../utils/request'
import { useStore } from 'vuex'
import { ref } from 'vue'
// 蒙层相关逻辑
const useMaskEffect = (showToaset) => {
  const router = useRouter()
  const route = useRoute()
  const store = useStore()
  const shopId = parseInt(route.params.id, 10)
  const { shopName, productList } = useCommonCartEffect(shopId)
  const showConfirm = ref(false)
  const handleConfirmOrder = async (isCanceled) => {
    const products = []
    for (let i in productList.value) {
      const product = productList.value[i]
      products.push({
        id: parseInt(product.__id, 10),
        num: product.count
      })
    }
    try {
      const result = await post('/api/order', {
        addressId: 1,
        shopId,
        shopName: shopName.value,
        isCanceled,
        products
      })
      if (result?.errno === 0) {
        store.commit('clearCartData', shopId)
        router.push({ name: 'OrderList' })
      } else {
        showToaset('操作失败，请稍后尝试')
      }
    } catch (e) {
      showToaset('失败')
    }
  }
  return { handleConfirmOrder, shopName, productList, showConfirm }
}
//
const useConfirmOrderEffect = (showConfirm) => {
  const handleSubmitClick = (status) => {
    showConfirm.value = status
    console.log(status)
  }
  return { handleSubmitClick }
}
export default {
  name: 'Order',
  components: { Toast },
  setup() {
    const route = useRoute()
    const shopId = parseInt(route.params.id, 10)
    const { calculations } = useCommonCartEffect(shopId)
    const { show, toasetMessage, showToaset } = useToasetEffect()
    const {
      handleConfirmOrder,
      shopName,
      productList,
      showConfirm
    } = useMaskEffect(showToaset)
    const { handleSubmitClick } = useConfirmOrderEffect(showConfirm)

    return {
      calculations,
      handleConfirmOrder,
      shopName,
      productList,
      show,
      toasetMessage,
      showConfirm,
      handleSubmitClick
    }
  }
}
</script>
<style lang='scss' scoped>
@import '../../style/viriables.scss';
.order {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  height: 0.49rem;
  line-height: 0.49rem;
  background: $bg-color;
  &__price {
    flex: 1;
    text-indent: 0.24rem;
    font-size: 0.14rem;
    color: #333;
  }
  &__btn {
    width: 0.98rem;
    background-color: #4fb0f9;
    color: $bg-color;
    text-align: center;
    font-size: 0.14rem;
  }
}
.mask {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 3rem;
    height: 1.56rem;
    background-color: #fff;
    border-radius: 0.04rem;
    text-align: center;
    &__title {
      margin-top: 0.24rem;
      line-height: 0.26rem;
      font-size: 0.18rem;
      color: #333;
    }
    &__desc {
      margin-top: 0.08rem;
      font-size: .14rem;
      color: #666;
    }
    &__btns {
      display: flex;
      margin: 0.24rem 0.58rem 0 0.58rem;
    }
    &__btn {
      flex: 1;
      width: 0.8rem;
      line-height: 0.32rem;
      border-radius: .16rem;
      &:first-child {
        margin-right: 0.12rem;
        border: 0.01rem solid #4fb0f9;
        color: #4fb0f9;
      }
      &:last-child {
        margin-left: 0.12rem;
        background-color: #4fb0f9;
        color: #fff;
      }
    }
  }
}
</style>
